.search-page-container {
  max-width: 600px;
  height: 100%;
  margin: 40px auto;
  padding: 20px;
  font-family: 'Pretendard', sans-serif;
  background: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
}

.search-input {
  width: 100%;
  padding: 12px 16px;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
  font-size: 1rem;
  transition: all 0.2s ease-in-out;
}

.search-input:focus {
  outline: none;
  border-color: #0d6efd;
  box-shadow: 0 0 8px rgba(13, 110, 253, 0.2);
}

.search-results {
  list-style: none;
  padding: 0;
  margin: 0;
}

.search-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px 12px;
  border-radius: 8px;
  margin-bottom: 8px;
  transition: all 0.2s ease;
  cursor: pointer;
  background: #fff;
}

.search-item:hover {
  background: #e7f1ff;
  transform: translateY(-2px);
}

.search-item i {
  font-size: 1.2rem;
  color: #0d6efd;
}

.result-link {
  text-decoration: none;
  color: #0d6efd;
  font-weight: 500;
  flex: 1;
}

.result-link:hover {
  text-decoration: underline;
}

@media screen and (max-width: 480px) {
  .search-page-container {
    margin: 20px;
    padding: 16px;
  }

  .search-item {
    flex-direction: column;
    align-items: flex-start;
    padding: 8px 10px;
  }

  .search-item i {
    margin-bottom: 4px;
    font-size: 1.1rem;
  }

  .search-input {
    font-size: 0.9rem;
    padding: 10px 14px;
  }
}
