.error-container {
  display: flex;
  background: #051D42;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  padding: 20px;
}

.error-code {
  font-size: 8rem;
  font-weight: bold;
  color: #ff4d4f;
  font-family: "";
  margin: 0;
}

.error-message {
  font-size: 2rem;
  margin: 20px 0;
  color: #ffffff;
  background: #051D42;
}

.error-btn {
  padding: 12px 24px;
  background-color: #4a90e2;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1.1rem;
  box-shadow: 0 4px 10px rgba(0,0,0,0.15);
}

.error-btn:hover {
  background-color: #357ab8;
}
