/* ===== 전체 컨테이너 ===== */
.container_posts {
  width: 800px;
  max-width: 100%;
  margin: 40px auto;
  padding: 20px;
  background: #fff;
}

/* ===== 제목 ===== */
h2 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 24px;
  color: #212529;
  border-bottom: 2px solid #f1f3f5;
  padding-bottom: 8px;
}

/* ===== 버튼 그룹 ===== */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-weight: 600;
  border-radius: 8px;
  transition: all 0.2s ease;
}


/* ===== 게시글 목록 ===== */
.list-group {
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.05);
}

.list-group-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 18px;
  font-size: 1rem;
  transition: background-color 0.2s ease, transform 0.1s ease;
  border: none;
  border-bottom: 1px solid #e9ecef;
}

.list-group-item:last-child {
  border-bottom: none;
}

/* Hover 시 부드럽게 강조 */
.list-group-item:hover {
  background-color: #f8f9fa;
  transform: translateY(-1px);
}

/* ===== 공지 스타일 ===== */
.list-group-item.notice {
  background-color: #fff5f5 !important;
  border-left: 4px solid #dc3545 !important;
  font-weight: 600;
}

.list-group-item.notice:hover {
  background-color: #ffe3e3 !important;
}

/* ===== 게시글 링크 ===== */
.list-group-item a {
  color: #212529;
  text-decoration: none;
}

.list-group-item a:hover {
  text-decoration: none;
}

/* ===== 게시글 메타 (오른쪽 날짜/좋아요) ===== */
.list-group-item div:last-child {
  font-size: 0.88rem;
  color: #6c757d;
  min-width: 110px;
  text-align: right;
}

/* 공지 아이콘 */
.list-group-item i {
  color: #dc3545;
  margin-right: 4px;
}

/* ===== 버튼 아이콘 + 텍스트 ===== */
.action-btn:hover {
  background-color: inherit; /* 부모 배경 색상 유지 */
  transform: none;           /* 확대/이동 제거 */
}

.action-btn {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-weight: bold;
  border-radius: 8px;
  transition: all 0.2s ease;
  background: none;
  color:#0b5ed7;
}

.action-btn i {
  font-size: 1.2rem;
}

/* 모바일에서는 텍스트 숨기고 아이콘만 */
@media (max-width: 768px) {
  .action-btn {
    width: 44px;
    height: 44px;
    justify-content: center;
    padding: 0;
  }

  .action-btn .btn-text {
    display: none;
  }
}


/* ===== 반응형 ===== */
@media (max-width: 768px) {
  .container_posts {
    padding: 10px;
    width: 370px;
    max-width: 90%;
  }

  .list-group-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 6px;
    padding: 12px 14px;
  }

  .list-group-item div:last-child {
    font-size: 0.8rem;
    color: #868e96;
    text-align: left;
  }

  .btn {
    font-size: 0.9rem;
    padding: 10px 14px;
  }

  h2 {
    font-size: 1.4rem;
  }
}

/* ===== 초소형 (480px 이하) ===== */
@media (max-width: 480px) {
  .list-group-item {
    padding: 10px 12px;
  }

  .list-group-item div:last-child {
    font-size: 0.75rem;
  }

  .btn {
    padding: 8px 12px;
    font-size: 0.85rem;
  }
}
