@import url("https://hangeul.pstatic.net/hangeul_static/css/nanum-square-neo.css");

.user-profile {
  font-family: "NanumSquareNeoBold";
  max-width: 600px;
  margin: 60px auto;
  padding: 40px;
  background: #ffffff;
  border-radius: 20px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.08);
  text-align: center;
  position: relative;
  overflow: hidden;
  animation: fadeInUp 0.7s ease-in-out;
}

/* 부드러운 등장 애니메이션 */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* 프로필 이미지 */
.profile-image {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  object-fit: cover;
  margin: 20px 0;
  border: 4px solid #4a90e2;
  box-shadow: 0 6px 15px rgba(74, 144, 226, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.profile-image:hover {
  transform: scale(1.07);
  box-shadow: 0 10px 25px rgba(74, 144, 226, 0.5);
}

/* 닉네임 */
.user-profile h2 {
  font-size: 1.8rem;
  color: #051d42;
  margin-bottom: 10px;
}

/* 이메일 / 가입일 */
.user-profile p {
  color: #666;
  font-size: 1rem;
  margin-bottom: 5px;
}

/* 최근 글 섹션 */
.user-profile h3 {
  margin-top: 30px;
  font-size: 1.3rem;
  color: #333;
  text-align: left;
  border-left: 4px solid #4a90e2;
  padding-left: 10px;
}

/* 글 목록 */
.user-profile ul {
  list-style: none;
  padding: 0;
  margin-top: 15px;
  text-align: left;
}

.user-profile li {
  background: #f9f9f9;
  padding: 12px 15px;
  border-radius: 10px;
  margin-bottom: 10px;
  transition: background 0.3s, transform 0.2s;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}

.user-profile li:hover {
  background: #eef5ff;
  transform: translateY(-3px);
}

.user-profile li a {
  color: #051d42;
  font-weight: bold;
  text-decoration: none;
}

.user-profile li a:hover {
  text-decoration: underline;
}

/* 날짜 */
.user-profile li span {
  color: #888;
  font-size: 0.85rem;
  margin-left: 5px;
}

/* 반응형 디자인 */
@media screen and (max-width: 600px) {
  .user-profile {
    margin: 20px;
    padding: 25px;
  }

  .profile-image {
    width: 110px;
    height: 110px;
  }

  .user-profile h2 {
    font-size: 1.5rem;
  }
}
