.Input_Group {
  position: relative;
  margin-bottom: 10px;
}

.Input_Group input {
  width: 100%;
  height: 40px;
  padding: 12px 10px 12px 35px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.Input_Group label {
  position: absolute;
  left: 35px;
  top: 44%;
  transform: translateY(-50%);
  font-size: 14px;
  color: #aaa;
  pointer-events: none;
  background-color: #fff;
  padding: 0 5px;
  transition: all 0.2s ease;
  z-index: 1;
}

.Input_Group input:focus + label,
.Input_Group input:not(:placeholder-shown) + label {
  top: -8px;
  font-size: 12px;
  color: #000000;
  font-weight: bold;
}

.Input_Group i {
  position: absolute;
  top: 44%;
  left: 10px;
  transform: translateY(-50%);
  font-size: 18px;
  color: #888;
  z-index: 2;
}
