.footer {
    background-color: #051D42;
    color: white;
    padding: 30px 20px;
    font-size: 0.9rem;
    display: flex;
    flex-direction: column;
    gap: 20px;
    position: fixed;
    left: 50%;
    bottom: 0;
    width: 100%;
    z-index: 100;
    transform: translateX(-50%);
    align-items: center;
    text-align: center;
    height: 50px;
    padding-bottom: 90px;
}



.footer a {
  color: white;
  text-decoration: none;
  transition: opacity 0.2s;
}

.footer a:hover {
  opacity: 0.8;
}

.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
}

.footer-top img {
  height: 50px;
}

.footer-links {
  display: flex;
  gap: 15px;
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

/* 모바일 대응 */
@media (max-width: 768px) {
  .footer {
    display: none;
  }
}
