.signup-container {
  width: 400px;
  margin: 50px auto;
  padding: 30px;
  border-radius: 12px;
  background: #fff;
  box-shadow: 0 6px 20px rgba(0,0,0,0.1);
  position: relative;
  font-family: 'Pretendard', sans-serif;
}

.signup-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.account-links {
  margin-top: 40px;
}

.account-links p {
  text-align: center;
  font-size: 0.78rem;
  color: #555;
}

.account-links p a {
  font-size: 0.9rem;
  color: #000;
}

/* ===================== 버튼 ===================== */
.signup-container button[type="submit"] {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 8px;
  background-color: var(--primary-color, #051D42);
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.1s;
}

.signup-container button[type="submit"]:hover {
  transform: scale(1.02);
}

/* ===================== 스텝 및 input ===================== */
.step {
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.4s ease;
  margin-bottom: 1rem;
  position: relative;
}

.step.active {
  opacity: 1;
  transform: translateY(0);
}

/* ===================== Input 그룹 & 플로팅 라벨 ===================== */
.Input_Group_S {
  position: relative;
  margin-bottom: 15px;
}

.Input_Group_S input {
  width: 100%;
  height: 40px;
  padding: 12px 10px 12px 40px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.Input_Group_S input:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0,123,255,0.3);
}

.Input_Group_S label {
  position: absolute;
  left: 35px;
  top: 44%;
  transform: translateY(-50%);
  font-size: 14px;
  color: #aaa;
  pointer-events: none;
  background-color: #fff;
  padding: 0 5px;
  transition: all 0.2s ease;
  z-index: 1;
}

.Input_Group_S input:focus + label,
.Input_Group_S input:not(:placeholder-shown) + label {
  top: -8px;
  font-size: 12px;
  color: #000000;
  font-weight: bold;
}

.Input_Group_S i {
  position: absolute;
  top: 44%;
  left: 10px;
  transform: translateY(-50%);
  font-size: 18px;
  color: #888;
  z-index: 2;
}

/* ===================== 파일 업로드 ===================== */
.file-upload-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem 0;
  width: 100%;
  opacity: 0;
  transform: translateY(-10px);
  animation: fadeInUp 0.4s forwards;
}

.file-upload-label {
  display: inline-block;
  text-align: center;
  padding: 12px 24px;
  border-radius: 8px;
  background-color: var(--primary-color, #051D42);
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.1s;
}

.file-upload-label:hover { 
  transform: scale(1.05); 
}

.file-upload-input { display: none; }

.file-upload-filename { 
  margin-top: 10px; 
  font-size: 0.9rem; 
  color: #333; 
  text-align: center; 
}

/* ===================== 체크박스 ===================== */
.terms {
  margin: 10px 0;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
}

.terms label {
  order: 1;
  margin-right: 6px;
}

.terms input[type="checkbox"] {
  order: 2;
  margin-left: 4px;
  margin-top: 12px;
  width: 14px;
  height: 14px;
}

.terms a {
  color: #4a90e2;
  text-decoration: underline;
}

/* ===================== SNS 로그인 버튼 ===================== */
.social-wrapper {
  margin-top: 1.5rem;
  text-align: center;
  opacity: 0;
  transform: translateY(-10px);
  animation: fadeInUp 0.5s forwards 0.3s;
}

.social-text {
  display: block;
  font-size: 0.9rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #444;
}

.social-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.social-buttons button {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  padding: 0;
}

.google-button {
  background: #fff;
  border: 1px solid #ddd;
}

.google-button:hover {
  background: #f2f2f2;
  transform: scale(1.15);
}

.google-button img {
  width: 28px;
  height: 28px;
  object-fit: contain;
}

.naver-button {
  background: #03c75a;
  border: 1px solid #03c75a;
}

.naver-button:hover {
  background: #02b255;
  transform: scale(1.15);
}

.naver-button img {
  width: 32px;
  height: 32px;
  object-fit: contain;
}

/* ===================== 애니메이션 keyframes ===================== */
@keyframes fadeInUp {
  0% { opacity: 0; transform: translateY(-10px); }
  100% { opacity: 1; transform: translateY(0); }
}

/* ===================== 모바일 반응형 ===================== */
@media screen and (max-width: 480px) {
  .signup-container {
    width: 90%;
    padding: 20px;
    margin: 30px auto;
  }

  .signup-container button[type="submit"] {
    padding: 10px;
    font-size: 0.9rem;
  }

  .file-upload-label {
    padding: 10px 20px;
    font-size: 0.9rem;
  }

  .social-buttons button {
    width: 40px;
    height: 40px;
  }

  .social-buttons button img {
    width: 24px;
    height: 24px;
  }
  .terms-checkbox label {
    font-size: 0.85rem;
  }
  .terms-checkbox label a {
    font-size: 0.9rem;
  }
  .terms-checkbox input[type="checkbox"] {
     order: 2;
     margin-left: 4px;
     margin-top: 14px;
     width: 8px;
     height: 8px; 
  }
}

@media screen and (max-width: 360px) {
  .signup-container h2 {
    font-size: 1.4rem;
  }

  .file-upload-filename {
    font-size: 0.8rem;
  }
}
