/* src/@bin/_CSS/verification.css */
.verification-container {
  max-width: 800px;
  margin: 40px auto;
  padding: 0 20px;
  font-family: 'Noto Sans KR', sans-serif;
}

.verification-container h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 1.8rem;
}

.verification-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.verification-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  margin-bottom: 12px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  background-color: #fff;
}

.verification-email {
  font-weight: 500;
}

.verification-actions button {
  margin-left: 8px;
  padding: 6px 12px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background 0.2s ease;
}

.verification-actions button:first-child {
  background-color: #4caf50;
  color: #fff;
}

.verification-actions button:first-child:hover {
  background-color: #45a049;
}

.verification-actions button:last-child {
  background-color: #f44336;
  color: #fff;
}

.verification-actions button:last-child:hover {
  background-color: #d32f2f;
}

.verification-link {
  margin-left: 12px;
  text-decoration: none;
  color: #2196f3;
  font-weight: 500;
}

.verification-link:hover {
  text-decoration: underline;
}
.custom-confirm-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 10, 10, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  animation: fadeIn 0.3s ease;
}

.custom-confirm-box {
  background: white;
  padding: 25px 30px;
  border-radius: 16px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.25);
  width: 320px;
  animation: slideUp 0.35s ease;
  text-align: center;
}

.custom-confirm-box h3 {
  margin-bottom: 15px;
  color: #051D42;
}

.custom-confirm-box input {
  width: 100%;
  margin: 6px 0;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 15px;
  outline: none;
  transition: border-color 0.2s;
}

.custom-confirm-box input:focus {
  border-color: #007bff;
}

.custom-confirm-buttons {
  margin-top: 12px;
  display: flex;
  justify-content: space-between;
}

.confirm-btn,
.cancel-btn {
  flex: 1;
  margin: 0 4px;
  padding: 8px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  color: #fff;
  font-weight: 500;
  transition: 0.2s;
}

.confirm-btn {
  background: #28a745;
}
.confirm-btn:hover {
  background: #218838;
}

.cancel-btn {
  background: #dc3545;
}
.cancel-btn:hover {
  background: #c82333;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(25px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}
